import React, { useState } from 'react';
import logo from './logo.svg';
import './App.css';
import { Homepage, TOC, Tools, Books, Lessons, Blogs, IAQ } from "./Pages/index"
import { NavBar, Foot } from "../src/Components/index"

function App() {
  const [route, changeRoute] = useState("lessonPlans");
  const home = route === "home";
  const lessonPlans = route === "lessonPlans";
  const tools = route === "tools";
  const books = route === "books";
  const blogs = route === "blogs";
  const iaq = route === "iaq";

  return (
    <div className="App">
      <div className={`${route === "home" ? "shortDownSpacer" : ""}`}>
        <NavBar
          route={route}
          onSelect={route => changeRoute(route)}
        />
      </div>
      {home && <Homepage />}
      {lessonPlans && <Lessons />}
      {books && <Books />}
      {tools && <Tools />}
      {blogs && <Blogs />}
      {iaq && <IAQ />}
      <Foot />
    </div>
  );
}

export default App;


